.mf-setting-dissolve {
  flex: 1 1;
  height: 100%;
  padding-top: 40px;
}
.mf-setting-dissolve .mf-setting-dissolve__title {
  font-size: 20px;
  color: #333333;
  letter-spacing: 0.19px;
  font-weight: bold;
  padding-left: 40px;
  height: 175px;
}
.mf-setting-dissolve .mf-setting-dissolve__content {
  text-align: center;
}
.mf-setting-dissolve .mf-setting-dissolve__content__desc {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}
.mf-setting-dissolve .mf-setting-dissolve__content__desc p {
  font-size: 16px;
  color: #333333;
  letter-spacing: 0;
  text-align: left;
}
.mf-setting-dissolve .mf-setting-dissolve__content__footer {
  margin-top: 40px;
}
.mf-setting__button {
  background: #FB4848;
  border-radius: 4px;
  color: #ffffff;
  width: 120px;
  height: 40px;
}
.mf-setting-modal .mf-setting-dissolve__header {
  height: 124px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: #333333;
  letter-spacing: 0.19px;
}
.mf-setting-modal .mf-setting-dissolve__content {
  font-size: 16px;
  color: #333333;
  letter-spacing: 0.16px;
  text-align: center;
}
.mf-setting-modal .mf-setting-dissolve__footer {
  height: 134px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.mf-setting-modal .mf-setting-dissolve__sort__header {
  height: 140px;
  text-align: center;
}
.mf-setting-modal .mf-setting-dissolve__sort__header .mf-setting-dissolve__sort__header__title {
  font-size: 20px;
  color: #333333;
  letter-spacing: 0.19px;
  padding-top: 44px;
}
.mf-setting-modal .mf-setting-dissolve__sort__header .mf-setting-dissolve__sort__header__desc {
  font-size: 16px;
  color: #333333;
  letter-spacing: 0.16px;
  line-height: 50px;
}
.mf-setting-modal .mf-setting-dissolve__sort__content {
  padding: 0px 40px;
}
.mf-setting-modal .mf-setting-dissolve__sort__content__input {
  width: 60%;
}
.mf-setting-modal .mf-setting-dissolve__sort__content__button {
  margin-left: 15px;
  height: 48px;
  position: relative;
  top: 2px;
}
.mf-setting-modal .mf-setting-dissolve_sort_footer {
  height: 100px;
  text-align: center;
  padding-top: 20px;
}
